import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'language'
})
export class LanguagePipe implements PipeTransform {
  languageSet = {
    'TAMIL': {
      'HELLO': '',
      'HOME': '',
      'SIGNUP':'',
      'LOGIN':'',
      'PAGENOTFOUND':' ',
      'USERNAME':' ',
      'PASSWORD':'',
      'SUBMIT':'',
      'EMPLOYEE':'',
      'DUMMY':''

    },
    'ENGLISH': {
      'HELLO': 'Hello',
      'HOME': 'Home',
      'SIGNUP':'Signup',
      'LOGIN':'login',
      'PAGENOTFOUND':'Page Not Found',
      'USERNAME':'UserName',
      'PASSWORD':'Password',
      'SUBMIT':'Submit',
      'EMPLOYEE':'Employee',
      'DUMMY':'Dummy'

    },
    'HINDI': {
      'HELLO': '',
      'HOME': '',
      'SIGNUP':'  ',
      'LOGIN':'  ',
      'PAGENOTFOUND':'  ',
      'USERNAME':' ',
      'PASSWORD':'',
      'SUBMIT':'',
      'EMPLOYEE':'',
      'DUMMY':''
    },
  }

  transform(value: any, args?: string): any {
    console.log(value);
    let language = localStorage.getItem('LANGUAGE');
    if(args){
      language = args;
    }
    if(this.languageSet[language][value]){
      return this.languageSet[language][value];
    }
    return value;
  }

}
